<template>
  <div class="vh-100 d-flex justify-content-center">
    <div class="form-access my-auto">
      <form>
        <span class="signin-span">Sign In</span>
        <div class="form-group">
          <!-- <input
            type="email"
            class="form-control"
            placeholder="Email Address"
            v-model="user.email"
            required
          /> -->
          <v-text-field
            solo
            dense
            :background-color="$vuetify.theme.dark ? '#2b2e3d' : '#ffffff'"
            color="info"
            v-model="user.email"
            placeholder="Email Address"
            hint="Input your Email"
            append-icon="mdi-account"
            clearable>
          </v-text-field>
        </div>
        <div class="form-group">
          <!-- <input
            type="password"
            class="form-control"
            placeholder="Password"
            v-model="user.password"
            required
          /> -->
          <v-text-field
            solo
            dense
            type="password"
            :background-color="$vuetify.theme.dark ? '#2b2e3d' : '#ffffff'"
            color="info"
            v-model="user.password"
            placeholder="Password"
            hint="Input your password"
            append-icon="mdi-lock"
            clearable>
          </v-text-field>
        </div>
        <div class="text-end">
          <router-link class="link-a" to="/reset">Forgot Password?</router-link>
        </div>
        <div class="custom-control">
          <!-- <input type="checkbox" class="form-check-input" id="form-checkbox" /> -->
          <v-checkbox
            color="info"
            :label="`Remember me`"
          ></v-checkbox>
          <!-- <label class="form-check-label" htmlFor="form-checkbox">
            Remember me
          </label> -->
        </div>
        <v-btn
          class="login-btn"
          block
          color="info"
          @click="loginClick">
          Sign in
        </v-btn>
        <!-- <button type="button" @click="loginClick" class="btn btn-primary">Sign In</button> -->
      </form>
      <h2>
        Don't have an account?
        <router-link to="/sign-up" class="link-a">Sign up here</router-link>
      </h2>
    </div>
  </div>
</template>
<script>
import { mapActions } from 'vuex'
import {setCookie, getCookie} from '@/common/cookie'
export default {
  data () {
    return {
      user:{
        email:'',
        password:''
      },
      rules: {
        required: value => !!value || 'Required.',
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'Invalid e-mail.'
        },
      },
      toPage:'/'
    }
  },
  created () {
    let token = getCookie('userToken')
    if(this.$route.query.page) {
      this.toPage = this.$route.query.page
    }
    if(token) {
      console.log(this.$route.path)
      this.$router.replace(this.toPage)
    }
  },
  mounted () {
    document.getElementsByName('body')
  },
  methods:{
    ...mapActions([
      "loginFetch"
    ]),
    async loginClick () {
      let res = await this.loginFetch({
        userName:this.user.email,
        userPassword:this.user.password
      })
      if(res.status === 0) {
        setCookie('userToken',res.userToken,3600)
        this.$router.push(this.toPage)
      }
    }
  }
}
</script>

