export default {
    changeTheme(state) {
      if (state.theme) {
        state.theme = false
      } else {
        state.theme = true
      }
    },
    setCurrentSymbol(state, v) {
      state.currentSymbol = v
    },
    setHighLowLine(state, v) {
      state.highLowLine = v
    },
    setStartNewBot(state, v) {
      state.startNewBot = v
    },
    setBalances(state, v) {
      state.balances = v
    },
    setReloadBotList(state, v) {
      state.reloadBotList = v
    },
  }
  