<template>
    <div class="telegram-page">
        <!-- <button @click="sendMsg">send MSG</button>
        <br>
        <button @click="update">update</button> -->
        <div class="content">
            <div class="left">
                <button @click="update">Update All</button>
                <button @click="sendMsg">send msg</button>
                <ul>
                    <li v-for="(item,index) in allData" :key="index">
                        {{item.message ? item.message.chat.username : 'Bot'}} : 
                        {{item.message ? item.message.text : ''}} &nbsp;&nbsp;&nbsp;&nbsp; <button @click="reply(item)"></button>
                    </li>
                </ul>
            </div>
            <div class="right">
                <!-- <button @click="update">Update Al1l</button> -->
            </div>
        </div>
        <div class="input-box" v-if="showInput">
            <input type="text" v-model="replyData.msg">
            <button @click="sendReply">
                send
            </button>
        </div>
    </div>
</template>

<script>
const token = '5432250982:AAE0ET5xV829ZQ_k0PpvtQpd2iSYBMTRypg'
const chat_id = '-1001563945250'
import axios from 'axios'
export default {
    data () {
        return {
            allData:[],
            replyData:{
                msg:'',
                id:''
            },
            showInput:false
        }
    },
    created () {
        this.init()
    },
    methods:{
        reply (data) {
            console.log(data)
            if(data.message) {
                this.replyData.id = data.message.chat.id
            }
            this.showInput = true
        },
        sendReply () {
            axios({
                method:'get',
                url:`https://api.telegram.org/bot${token}/sendMessage?chat_id=${this.replyData.id}&text=${this.replyData.msg}`
            }).then(res => {
                console.log(res)
            })
        },
        update () {
            axios({
                method:'get',
                url:`https://api.telegram.org/bot${token}/getUpdates`
            }).then(res => {
                console.log(res)
                if(res.data.ok) {
                    this.allData = res.data.result
                }
            })
        },
        sendMsg () {
            https://api.telegram.org/bot<token>/METHOD_NAME
            
            axios({
                method:'get',
                url:`https://api.telegram.org/bot${token}/sendMessage?chat_id=${chat_id}&text=Hi,bot`
            }).then(res => {
                console.log(res)
            })
        },
        init () {
            axios({
                method:'get',
                url:`https://api.telegram.org/bot${token}/getMe`
            }).then(res => {
                console.log(res)
            })
        }
    }
}
</script>

