<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'
export default {
  data() {
    return {
      botData: {
        botType: '0',
        exchangeName: 'binance_0',
        symbol: 'BTC/USDT',
        stake: 10000,
        maxStake: 15000,
        high: 30000,
        low: 20000,
        level: 100,
        tp: '0',
        tpPrice: 0,
        sl: '0',
        slPrice: 0,
        trailUp: "1",
        trailDown: "0",
      },
      loading:false,
      showAlt:{
        show:false,
        msg:'',
        type:'success'
      }
    }
  },
  props:{
    posterBotData:{
      type:Object,
      default: () => {}
    }
  },
  created () {
    if(Object.keys(this.posterBotData).length > 0) {
      let data = {
        ...this.posterBotData,
        tp:this.posterBotData.tp ? '1' : '0',
        sl:this.posterBotData.sl ? '1' : '0',
        trailUp:this.posterBotData.trailUp ? '1' :'0',
        trailDown:this.posterBotData.trailDown ? '1' : '0'
      }
      this.botData = data
    }
  },
  computed: {
    ...mapGetters(['getBalances']),
    currentBalance() {
      let data = JSON.parse(JSON.stringify(this.getBalances))
      let obj = {}
      let symbol1 = this.botData.symbol.split('/')
      data.forEach((v) => {
        for (let k in v) {
          obj[k] = {}
          obj[k] = v[k]['balances']
        }
      })
      if (Object.keys(obj).length === 0) {
        return ['--', '--']
      }
      let coin1 = obj[this.botData.exchangeName].filter((v) => {
        return v.asset === symbol1[0]
      })
      let coin2 = obj[this.botData.exchangeName].filter((v) => {
        return v.asset === symbol1[1]
      })
      let asset1 = 0,
        asset2 = 0
      if (coin1.length > 0) {
        asset1 = parseFloat(coin1[0].free) + parseFloat(coin1[0].locked)
      }
      if (coin2.length > 0) {
        asset2 = parseFloat(coin2[0].free) + parseFloat(coin2[0].locked)
      }
      return [asset1, asset2]
    },
  },
  methods: {
    ...mapActions(['createBotFetch']),
    ...mapMutations(['setHighLowLine', 'setReloadBotList']),
    startClick() {
      this.setReloadBotList(true)
    },
    async startbot() {
      this.loading = true
      let res = await this.createBotFetch(this.botData)
      this.loading = false
      if (res.status === 0) {
        this.showAlt.show = true
        this.showAlt.msg = 'Create Bot success!'
        this.showAlt.type = 'success'
        setTimeout(() => {
          this.showAlt.show = false
        },2000)
        this.startClick()
      }
    },
    highLowChange() {
      this.setHighLowLine({
        high: this.botData.high,
        low: this.botData.low,
      })
    },
  },
}
</script>
