import axios from 'axios'
import config from '@/config'
import bcryptSupport from '@/common/bcrypt-support'
// axios
const service = axios.create({
  baseURL: config.BASEURL, // apibase_url
  timeout: 5000, // ,
  headers: {
    "Content-Type": "application/json"
  },
})
const hashParams = [
  "userPassword",
]
// request
service.interceptors.request.use(config => {
    let data;
    if(config.method === 'post') {
        data = config.data && JSON.parse(JSON.stringify(config.data));
    }else {
        data = config.params && JSON.parse(JSON.stringify(config.params));
    }
    
    for(const item in data) {
        if(hashParams.indexOf(item) > -1) {
            data[item] = bcryptSupport(data[item])
        }
    }
    if(config.method === 'post') {
        config.data = data;
    }else {
        config.params = data;
    }
    
    return config;
}, error => {
    Promise.reject(error)
})

// respone
service.interceptors.response.use(
    response => {
        if(response.status == 200) {
            return response.data
        }else {
            return 'err'
        }
    },
    error => {
        return Promise.reject(error)
    })

export default service