<script>
import { mapMutations, mapGetters } from 'vuex'
export default {
  data () {
    return {
      stepIndex:1,
      btnText:'Continue',
      pairs:[
        "BTC/USDT",
        "ETH/USDT",
        "BNB/USDT"
      ],
      exchanges:[
        "binance_0"
      ],
      botData: {
        botType: 0,
        exchangeName: 'binance_0',
        symbol: 'BTC/USDT',
        stake: 10000,
        maxStake: 15000,
        high: 30000,
        low: 20000,
        level: 100,
        tp: false,
        tpPrice: 0,
        sl: false,
        slPrice: 0,
        trailUp: true,
        trailDown: false,
      },
      showBack:false
    }
  },
  created () {
    if(this.cbotData && this.cbotData.type) {
      this.botData.botType = this.cbotData.type
    }
  },
  computed:{
    ...mapGetters(['getBalances']),
    currentBalance() {
      let data = JSON.parse(JSON.stringify(this.getBalances))
      let obj = {}
      let symbol1 = this.botData.symbol.split('/')
      data.forEach((v) => {
        for (let k in v) {
          obj[k] = {}
          obj[k] = v[k]['balances']
        }
      })
      if (Object.keys(obj).length === 0) {
        return ['--', '--']
      }
      let coin1 = obj[this.botData.exchangeName].filter((v) => {
        return v.asset === symbol1[0]
      })
      let coin2 = obj[this.botData.exchangeName].filter((v) => {
        return v.asset === symbol1[1]
      })
      let asset1 = 0,
        asset2 = 0
      if (coin1.length > 0) {
        asset1 = parseFloat(coin1[0].free) + parseFloat(coin1[0].locked)
      }
      if (coin2.length > 0) {
        asset2 = parseFloat(coin2[0].free) + parseFloat(coin2[0].locked)
      }
      return [asset1, asset2]
    },
    stepText () {
      let text = ''
      switch (this.stepIndex) {
        case 1:
          text = 'General settings'; break;
        case 2:
          text = 'Investment'; break;
        case 3:
          text = 'Grid setting'; break;
        case 4:
          text = 'Advance setting'; break;
        default:
          text = ''; break;
      }
      return text;
    }
  },
  props:{
    cbotData:{
      type:Object,
      default: () => {}
    }
  },
  methods: {
    ...mapMutations(['setStartNewBot']),
    close() {
      this.setStartNewBot(0)
    },
    stepBackClick () {
      if(this.stepIndex <= 2) {
        this.showBack = false
      }
      this.stepIndex -= 1
    },
    stepClick () {
      if(this.stepIndex === 4) {
        this.$emit('bot-comfirm',this.botData)
        this.close()
      }
      this.showBack = true
      if(this.stepIndex < 4) {
        this.stepIndex += 1;
      }
      if(this.stepIndex === 4) {
        this.btnText = 'comfirm'
      }
    }
  },
}
</script>

