<script>
import Loading from '../components/Loading.vue'
import { mapActions, mapGetters, mapMutations } from 'vuex'
export default {
  components:{
    Loading
  },
  data() {
    return {
      type: 0,
      bots: [],
      balances: [],
      historyOrder: [],
      accountType: 'ALL',
      assetType: 'ALL',
      accountType2: 'ALL',
      assetType2: 'ALL',
      botIDType:'ALL',
      closeLoading:false,
      tableState:{
        bot:false,
        closeBot:false,
        balance:false,
        history:false
      }
    }
  },
  //   props: ['isReload'],
  computed: {
    ...mapGetters(['getReloadBotList']),
    botsList() {
      let bots = this.bots
      let result = []
      if (this.type === 0) {
        result = bots.filter((v) => {
          return v.status === 1 || v.status === 0
        })
      } else {
        result = bots.filter((v) => {
          return v.status === 2
        })
      }
      return result
    },
    balancesList() {
      let data = JSON.parse(JSON.stringify(this.balances))
      data.forEach((item) => {
        for (let account in item) {
          item[account]['balances'].forEach((v) => {
            v.account = account
          })
        }
      })

      let copyData = []
      if (this.accountType === 'ALL') {
        data.forEach((item) => {
          for (let account in item) {
            copyData = copyData.concat(item[account]['balances'])
          }
        })
      } else {
        let obj = {}
        data.forEach((v) => {
          for (let k in v) {
            obj[k] = {}
            obj[k] = v[k]
          }
        })
        copyData = obj[this.accountType]['balances']
      }
      if (this.assetType !== 'ALL') {
        copyData = copyData.filter((v) => {
          return v.asset === this.assetType
        })
      }
      copyData = copyData.filter((v) => {
        return !(v.free === 0 && v.locked === 0)
      })
      return copyData
    },
    orderList() {
      let data = JSON.parse(JSON.stringify(this.historyOrder))
      data.forEach((item) => {
        for (let account in item) {
          item[account]['orders'].forEach((v) => {
            v.account = account
          })
        }
      })

      let copyData = []
      if (this.accountType2 === 'ALL') {
        data.forEach((item) => {
          for (let account in item) {
            copyData = copyData.concat(item[account]['orders'])
          }
        })
      } else {
        let obj = {}
        data.forEach((v) => {
          for (let k in v) {
            obj[k] = {}
            obj[k] = v[k]
          }
        })
        copyData = obj[this.accountType2]['orders']
      }
      if (this.botIDType !== 'ALL') {
        copyData = copyData.filter((v) => {
          return v.bot_id === this.botIDType
        })
      }
      if (this.assetType2 !== 'ALL') {
        copyData = copyData.filter((v) => {
          return v.symbol === this.assetType2
        })
      }
      return copyData
    },
    accountList() {
      let data = this.balances
      let arr = []
      if (data.length > 0) {
        data.forEach((v) => {
          arr.push(...Object.keys(v))
        })
      }
      arr.unshift('ALL')
      return arr
    },
    accountList2() {
      let data = this.historyOrder
      let arr = []
      if (data.length > 0) {
        data.forEach((v) => {
          arr.push(...Object.keys(v))
        })
      }
      arr.unshift('ALL')
      return arr
    },
    assetList() {
      let data = this.balances
      let arr = []
      data.forEach((item) => {
        for (let account in item) {
          item[account]['balances'].forEach((v) => {
            arr.push(v.asset)
          })
        }
      })
      arr = [...new Set(arr)]
      arr.unshift('ALL')
      return arr
    },
    assetList2() {
      let data = this.historyOrder
      let arr = []
      data.forEach((item) => {
        for (let account in item) {
          item[account]['orders'].forEach((v) => {
            arr.push(v.symbol)
          })
        }
      })
      arr = [...new Set(arr)]
      arr.unshift('ALL')
      return arr
    },
    botIDList () {
      let data = this.historyOrder
      let arr = []
      data.forEach((item) => {
        for (let account in item) {
          item[account]['orders'].forEach((v) => {
            arr.push(v.bot_id)
          })
        }
      })
      arr = [...new Set(arr)]
      arr.unshift('ALL')
      return arr
    },
  },
  methods: {
    ...mapActions([
      'botListFetch',
      'portfolioBalancesFetch',
      'orderListOrderFetch',
      'stopBotFetch',
      'resumeBotFetch',
      'pauseBotFetch',
    ]),
    ...mapMutations(['setReloadBotList']),
    exportClick() {
      let header = ["Account","Symbol","Type","Price","Amount","Bot ID","Time"]
      let ble = ['account','symbol','side','price','executed_qty','bot_id','update_timestamp']
      const table = header.join(",") +
        "\n" + this.orderList.map((item) => {
            let result = "";
            ble.forEach(key => {
              let text = `${item[key] + "\t"},`
              if(key === 'update_timestamp') {
                text = `${this.formatTime(item[key])}\t`
              }
              result += text;
            })
            return result;
          }).join("\n");
      let uri = "data:text/csv;charset=utf-8,\ufeff" + encodeURIComponent(table);
      var link = document.createElement("a");
      link.href = uri;
      link.download = `${new Date()}.csv`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    async stopClicked(botId) {
      this.closeLoading = true
      let res = await this.stopBotFetch({
        botId,
      })
      this.closeLoading = false
      if (res.status === 0) {
        this.getBotList()
      }
    },
    typeClick(type) {
      this.type = type
      if (type === 0 || type === 1) {
        this.getBotList()
      } else if (type === 2) {
        this.getBalanceList()
      } else if (type === 3) {
        this.getHistoryList()
      }
    },
    async switchStatusClicked(item) {
      let fun = item.status === 0 ? 'pauseBotFetch' : 'resumeBotFetch'
      let res = await this[fun]({
        botId: item.id,
      })
    },
    async getHistoryList() {
      let res = await this.orderListOrderFetch()
      if (res.status === 0) {
        this.tableState.history = true
        this.historyOrder = res.exchanges
      }
    },
    async getBotList() {
      let res = await this.botListFetch()
      if (res.status === 0) {
        this.tableState.bot = true
        this.tableState.closeBot = true
        this.bots = res.bots
      }
    },
    async getBalanceList() {
      let res = await this.portfolioBalancesFetch()
      if (res.status === 0) {
        this.tableState.balance = true
        this.balances = res.exchanges
      }
    },
    formatType(type) {
      switch (type) {
        case 0:
          return 'Long Bot'
        case 1:
          return 'Short Bot'
        case 2:
          return 'Closed bot'
        case 7:
          return 'Unlimited Long Grid'
        case 8:
          return 'Unlimited Short Grid'
        case 9:
          return 'Unlimited Long Trend'
        case 10:
          return 'Unlimited Short Trend'
        default:
          return ''
      }
    },
    formatTime(time) {
      let date = new Date(time).toLocaleString()
      return date
    },
  },
  created() {
    this.getBotList()
  },
  watch: {
    getReloadBotList(v) {
      this.type = 0
      this.getBotList()
      this.setReloadBotList(false)
    },
  },
}
</script>
