<template>
<div class="exchange-page primary" data-app="true">
    <v-row>
        <v-col class="col-height" cols="12" lg="3" sm="12" xs="12">
          <MarketPairs/>
        </v-col>
        <v-col class="col-height" cols="12" lg="6" sm="12" xs="12">
            <TradingChart v-if="$route.path == '/exchange' && !$vuetify.theme.dark"/>
            <TradingChartDark v-if="$route.path == '/exchange' && $vuetify.theme.dark"/>
        </v-col>
        <v-col class="col-height" cols="12" lg="3" sm="12" xs="12">
            <!-- <Strategy /> -->
            <Strategy :posterBotData="botData" v-if="getStartNewBot === 0 || getStartNewBot === 1" />
            <BotPoster @bot-comfirm="botComfirm" :cbotData="cbotData" v-else />
        </v-col>
    </v-row>
    <v-row>
        <v-col cols="12" lg="3" sm="12" xs="12">
            <MarketNews />
        </v-col>
        <v-col cols="12" lg="9" sm="12" xs="12">
            <HistoryOrder />
        </v-col>
    </v-row>
    <StartNewBot @cbot-click="cBotClick" v-if="getStartNewBot === 1" />
</div>
</template>

<script>
import MarketPairs from "../components/MarketPairs.vue";
import TradingChart from "../components/TradingChart.vue";
import TradingChartDark from "../components/TradingChartDark.vue";
import Strategy from "../components/Strategy.vue";
import MarketNews from "../components/MarketNews.vue";
import HistoryOrder from "../components/HistoryOrder.vue";
import StartNewBot from '../components/StartNewBot.vue'
import BotPoster from '../components/BotPoster.vue'
import { getCookie} from '@/common/cookie'
import { mapActions, mapGetters, mapMutations } from "vuex";
export default {
    components:{
        MarketPairs,
        TradingChart,
        TradingChartDark,
        Strategy,
        MarketNews,
        HistoryOrder,
        StartNewBot,
        BotPoster,
    },
    data () {
        return {
            cbotData:'',
            botData:{}
        }
    },
    computed:{
        ...mapGetters([
            "getStartNewBot"
        ])
    },
    created () {
        // console.log(this.$route.query.page)
        console.log(window.Telegram.WebApp)
        if(!getCookie('userToken')) {
            this.$router.replace(`/login?page=${this.$route.query.page}`)
            return
        }
        let theme = localStorage.getItem('theme')
        

        // if(theme == 'true') {
        //     this.$vuetify.theme.dark = true
        // }else {
        //     this.$vuetify.theme.dark = false
        // }
        this.getBalanceList()
    },
    methods:{
        ...mapActions(['portfolioBalancesFetch']),
        ...mapMutations(['setBalances']),
        async getBalanceList() {
            let res = await this.portfolioBalancesFetch()
            if (res.status === 0) {
                this.setBalances(res.exchanges)
            }
        },
        cBotClick (data) {
            this.cbotData = data
        },
        botComfirm (data) {
            this.botData = data
        }
    }
}
</script>
<style lang="less" scoped>
.exchange-page {
    padding:20px 50px 100px;
}
.col-height {
    height: 570px;
    overflow: hidden;
}
@media screen and (max-width:576px){
    .exchange-page {
        padding:10px;
    }
    
}
</style>